.topmenu {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	border-bottom: 1px solid #1b1b1b;
	background: #1d1d1d;
	color: #f5f5f5;
	// display: flex
 // // flex-wrap: nowrap
 // // flex-direction: row
	// .pull-left,
	// .pull-right
	// 	display: flex
	// 	flex-wrap: nowrap
	// 	flex-direction: row
	.dropdown-menu {
		background: #ffffff;
		z-index: 9999; }
	.btn-group {
		.btn {
			border: 0 !important;
			border-radius: 0 !important;
			box-shadow: none;
			padding: 5px 10px;
			background: none;
			color: #909090;
			&:hover, &.active {
				background: #000;
				color: #fff; } } }
	.custom-file {
		input {
			display: none; }
		label {
			cursor: pointer;
			margin: 0;
			font-weight: initial; } } }

.no-padding {
	padding: 0;
	margin: 0; }
.col-right {
	overflow-y: auto;
	padding: 15px;
	* {
		border-radius: 0; }
	.form-control {
		&:focus {
			outline: none;
			box-shadow: none; } } }

.modal {
	.modal-content {
		border-radius: 0;
		background: #252525;
		color: #fff;
		.modal-header,
		.modal-footer {
			padding: 8px 15px;
			border-color: #0e0e0e;
			background: #404040;
			.close {
				color: #fff; }
			.btn {
				border-radius: 0;
				padding: 3px 12px;
				color: #d4d4d4;
				background-color: #404040;
				border-color: #0e0e0e;
				&:active, &:hover {
					background: #252525; } } }

		.modal-footer {
			background: #181818; }
		.modal-body {
			ul {
				margin: 0;
				padding: 0;
				list-style-type: none;
				display: flex;
				flex-wrap: wrap;
				li {
					flex: 0 0 50%;
					max-width: 50%;
					a {
						text-decoration: none;
						cursor: pointer;
						color: #808080;
						display: flex;
						align-items: center;
						padding: 7px 0;
						border-radius: 3px;
						i {
							font-size: 42px; } }
					&:hover {
						a {
							color: #fff;
							background: #0e0e0e; } } } }
			input, select {
				background: #0e0e0e;
				border-color: #0e0e0e;
				&:active, &:hover, &:focus {
					box-shadow: none; } } } } }

input.custom-range {
	width: 100%;
	height: calc(1rem + 0.4rem);
	padding: 0;
	background-color: transparent;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	background: none !important;
	border: 0 !important;
	&:focus {
		outline: 0;

		&::-webkit-slider-thumb, &::-moz-range-thumb, &::-ms-thumb {
			box-shadow: 0 0 0 1px #1d1d1d, 0 0 0 0.2rem rgba(0, 123, 255, 0.25); } }

	&::-moz-focus-outer {
		border: 0; }

	&::-webkit-slider-thumb {
		width: 1rem;
		height: 1rem;
		margin-top: -0.25rem;
		background-color: #969696;
		border: 0;
		border-radius: 1rem;
		transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
		-webkit-appearance: none;
		appearance: none;

		&:active {
			background-color: #000; } }

	&::-webkit-slider-runnable-track {
		width: 100%;
		height: 0.5rem;
		color: transparent;
		cursor: pointer;
		background-color: #1d1d1d;
		border-color: transparent;
		border-radius: 1rem; }

	&::-moz-range-thumb {
		width: 1rem;
		height: 1rem;
		background-color: #969696;
		border: 0;
		border-radius: 1rem;
		transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
		-moz-appearance: none;
		appearance: none;

		&:active {
			background-color: #000; } }

	&::-moz-range-track {
		width: 100%;
		height: 0.5rem;
		color: transparent;
		cursor: pointer;
		background-color: #1d1d1d;
		border-color: transparent;
		border-radius: 1rem; }

	&::-ms-thumb {
		width: 1rem;
		height: 1rem;
		margin-top: 0;
		margin-right: 0.2rem;
		margin-left: 0.2rem;
		background-color: #969696;
		border: 0;
		border-radius: 1rem;
		transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
		appearance: none;

		&:active {
			background-color: #000; } }

	&::-ms-track {
		width: 100%;
		height: 0.5rem;
		color: transparent;
		cursor: pointer;
		background-color: transparent;
		border-color: transparent;
		border-width: 0.5rem; }

	&::-ms-fill-lower {
		background-color: #1d1d1d;
		border-radius: 1rem; }

	&::-ms-fill-upper {
		margin-right: 15px;
		background-color: #1d1d1d;
		border-radius: 1rem; }

	&:disabled {
		&::-webkit-slider-thumb {
			background-color: #1d1d1d; }

		&::-webkit-slider-runnable-track {
			cursor: default; }

		&::-moz-range-thumb {
			background-color: #1d1d1d; }

		&::-moz-range-track {
			cursor: default; }

		&::-ms-thumb {
			background-color: #1d1d1d; } } }


.topmenu .btn-group {
	&.range {
		display: flex;
		align-items: center;
		.btn {
			&.ok-range {
				&:hover {
					background: none; }
				input.custom-range {
					&::-webkit-slider-runnable-track {
						background-color: #333 !important; }
					&::-ms-track {
						background-color: #333 !important; }
					&::-moz-range-track {
						background-color: #333 !important; } } } } } }

@media (prefers-reduced-motion: reduce) {
	.custom-range::-webkit-slider-thumb {
		transition: none; } }

@media (prefers-reduced-motion: reduce) {
	.custom-range::-moz-range-thumb {
		transition: none; } }

@media (prefers-reduced-motion: reduce) {
	.custom-range::-ms-thumb {
		transition: none; } }
.colorpicker .dropdown-menu {
	background: #1d1d1d;
	&::after, .close {
		display: none; } }
